export function createChartDom(createChartFunc,domNode,onHover,height){var chart=createChartFunc(domNode,{height,layout:{background:{color:'transparent'}},crosshair:{vertLine:{visible:false},horzLine:{visible:false}},grid:{vertLines:{visible:false},horzLines:{visible:false}},timeScale:{visible:false,fixLeftEdge:true,fixRightEdge:true,lockVisibleTimeRangeOnResize:true},rightPriceScale:{visible:false},handleScale:{pinch:false,mouseWheel:false}});var handleResize=function(){chart.applyOptions({width:domNode.clientWidth});};chart.subscribeCrosshairMove(function({time,seriesPrices}){onHover({time,price:seriesPrices.values().next().value});});chart.timeScale().fitContent();window.addEventListener('resize',handleResize);globalThis._onekey_chart=chart;return{chart,handleResize};}export function updateChartDom({lineColor,topColor,bottomColor,data}){var formattedData=data.map(function([time,value]){return{time,value};});var chart=globalThis._onekey_chart;if(!chart._onekey_series){var newSeries=chart.addAreaSeries({lineColor,topColor,bottomColor,lineWidth:2,crosshairMarkerBorderColor:'#fff',crosshairMarkerRadius:5});newSeries.setData(formattedData);chart._onekey_series=newSeries;return;}var series=chart._onekey_series;series.applyOptions({lineColor,topColor,bottomColor});series.setData(formattedData);if(data.length>2){chart.timeScale().setVisibleLogicalRange({from:0.4,to:data.length-1.4});}}